@import '../../components/ComponentsScss/Variables';
@import '../../components/ComponentsScss/mixins';

.contacts {
    padding: 60px 0 100px 0;
}

.contacts__title {
    @include adaptiv-font(52, 32);
    color: $lightOrange;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 50px;
    font-weight: 600;
}

.contacts__content {
    @include adaptiv-font(26, 18);
    display: flex;
    gap: 30px;
    align-items: center;
    justify-content: space-between;   
    font-weight: 500;
}

.contact__address {
    display: flex;
    flex-direction: column;
    gap: 30px;
    text-align: center;
}

.contact__number,
.contact__email {
    color: $greyText;
    @include adaptiv-font(24, 16);  
    font-weight: 500;
    background-image: linear-gradient(currentColor, currentColor);
    background-position: 0% 100%;
    background-repeat: no-repeat;
    background-size: 0% 2px;
    transition: background-size 0.3s;
    &:hover {
        background-size: 100% 2px;
    }
}

.contact__map {
    flex: 1 0 50%;
}

.map {
    width: 100%;
    height: 400px;
}

@include md {
    .contacts__content {
        flex-wrap: wrap;
        justify-content: center;
    }
    .column-footer__icons {
        align-self: center;
    }
}

@include sm {
    .map {
        height: 300px;
    }
}
