@use './Variables.scss';

%container {
    max-width: $ul;
    margin: 0 auto;
    padding: 0 $gap;
}

$maxWidth: 1440;
@mixin adaptiv-font($pcSize, $mobSize) {
    $addSize: $pcSize - $mobSize;
    $maxWidth: $maxWidth - 320;
    font-size: calc(
        #{$mobSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth})
    );
}

/* ul */
@mixin ul-min {
    @media (min-width: $ul) {
        @content;
    }
}

@mixin ul {
    @media (max-width: $ul) {
        @content;
    }
}

@mixin ul-xl {
    @media (max-width: $ul) and (min-width: $xl) {
        @content;
    }
}

@mixin ul-lg {
    @media (max-width: $ul) and (min-width: $lg) {
        @content;
    }
}

@mixin ul-md {
    @media (max-width: $ul) and (min-width: $md) {
        @content;
    }
}

@mixin ul-sm {
    @media (max-width: $ul) and (min-width: $sm) {
        @content;
    }
}

@mixin ul-xs {
    @media (max-width: $ul) and (min-width: $xs) {
        @content;
    }
}

